#support {
  @include xs-block {
    background: no-repeat url("../img/svg/Support_bg_xs.svg");
    background-position: center  665px;
  }
  @include xl-block {
    background: none;
  }
}

.support {
  @include xs-block {
    background: none; 
  }

  @include xl-block {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: no-repeat url("../img/Suport_bg.png");
    background-size: 48% 100% ;
    background-position: 100% 0;
  }

 
      
    .title {
        font-size: 48px;
      }

    .content {
      @include size-xs(13);
      @include size-xl(13);
      @include xs-block {
        flex-direction: column;
      }
      @include xl-block {
        flex-direction: row;
        justify-content: space-between;
      }
        display: flex;


        .description {
          @include size-xs(13);
          @include xl-block {
            width: 480px
          }
            @include description();
            width: 443px;

            .text {
              @include xs-block {
                text-align: left;
                margin-bottom: 120px;
              }
              @include xl-block {
                text-align: left;
                margin-bottom: 120px;
              }
                color: #000;
                font-size: 18px;
                line-height: 24px;
                font-weight: 300;
      
                a {
                  color: #0072FF;
                  &:hover {
                      text-decoration-line: underline;
                  }
              }
            }
          }
      
          .viwe {
            @include size-xs(13);
           @include xl-block {
             width: 540px;
           }
            @include xs-block {
              position: relative;
              margin:0;
              margin-bottom: 60px;
           

            }
              width: 540px;
              margin-left: 215px;
              .suport_list {
                @include xs-block {
                  
                }
                  .item {
                    @include size-xs(13);
                    @include xs-block {
                      background-position: 285px center;
                      width: 315px;
                    }
                    @include xl-block {
                      background-position: 380px center;
                      width: 420px;
                    }
                    box-sizing: border-box;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    width: 420px;
                    height: 100px;
                    border-radius: 28px;
                    margin-bottom: 30px;
                    padding-left: 25px;
                    background: no-repeat url("../img/svg/Suport_link_arr.svg"), #FFFFFF;
                    background-position: 380px center;


                    h4 {
                        margin-bottom: 10px;
                    }
                     p {
                        margin: 0;
                        font-size: 14px;
                        line-height: 22px;
                        color: #9297A2;
                        font-size: 300;
                     }
                  }
              }
          }
    }
}