.join_to_us {
    // height: 73.7vh;
    // @include size-xs(13);
    display: flex;
    flex-direction: column;

    .pro_title {
        line-height: 100px;
        margin-bottom: 30px;
        @include xl-block {
            margin-bottom: 0;
        }
    }

    .content {
        @include xs-block {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    
        @include xl-block {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            justify-content: space-between;
        }
    }
    
    
    // @include section;

    padding-top: 11vh;
    // justify-content: space-between;

    .description {
        @include size-xs(13);
        @include size-xl(6);

        @include xl-block {
            order: 1;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 120px;
        }

        @include xs-block {
            order: 2;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 120px;
        }

        @include description;

     

        .pro_text {
            font-size: 20px;
            line-height: 34px;
            font-weight: 300;
            margin-bottom: 37px;

            p {
                margin-bottom: 40px;
            }
        }

        .btn {
            color: white;
            line-height: 60px;
            text-align: center;
            width: 300px;
            height: 60px;
            background: linear-gradient(0deg, #050B11, #050B11);
            box-shadow: 0px 2px 0px rgba(255, 255, 255, 0.0996943);
            border-radius: 30px;
            font-size: 22px;
            @include xl-block {
                margin-top: -20px;
            }
            
            

            &:hover {
                color: #0072FF;
                transition: color 0.3s;
            }
        }
    }

    .viwe {
        @include size-xs(13);
        @include xs-block{
            order: 1;
            margin-top: 60px;
        }
        @include xl-block{
            position: relative;
            order: 2;
            margin-top: 60px;
            width: 421px;
            top: -145px;
            left: -49px;
        }

        width: 50%;
        display: flex;
        justify-content: center;

        .action {
            width: 66.8%;

            @include xs-block {
                 width: 100%;   
            }

            .action_item {
                position: relative;
                color: #9297A2;
                background: #fff;
                margin-bottom: 30px;
                border-radius: 28px;
                padding: 22px 0 26px 80px;


                &:nth-child(1) {
                    &::before {
                        content: "1";

                        @include action_item;
                    }
                }

                &:nth-child(2) {
                    ul {
                        padding-left: 15px;
                        list-style-type: disc;

                        li {
                            font-size: 14px;
                            line-height: 22px;
                            font-weight: 300;
                            margin: 0;
                        }
                    }

                    &::before {
                        content: "2";

                        @include action_item;
                    }
                }

                &:nth-child(3) {
                    p {
                        a {
                            color: #0072FF;

                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }

                    &::before {
                        content: "3";

                        @include action_item;
                    }
                }

                h3 {
                    font-size: 20px;
                    line-height: 22px;
                    color: #000;
                    margin: 5px 0;
                    font-weight: 500;
                }

                h4 {
                    font-size: 14px;
                    line-height: 22px;
                    margin: 0;
                    font-weight: 400;
                }

                p {
                    font-size: 14px;
                    line-height: 22px;
                    font-weight: 300;
                    margin: 0;
                }
            }
        }
    }
}

.hom_you_can_work,
.how_earn {
    @include size-xs(13);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;

    .pro_title {
        line-height: 100px;
        padding: 120px 0 60px 0;
        border-bottom: 1px solid #E5E5E5;

        @include xs-block {
         line-height: 56px;
         border: none;
        }
        @include xl-block {
            padding: 120px 0 60px 0;
            border-bottom: 1px solid #E5E5E5;
           }
    }

    .benefits__list {
        @include size-xs(13);
        padding-top: 60px;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        flex-wrap: wrap;

        .benefit__item {
            width: 540px;
            padding-bottom: 30px;
            position: relative;


            h3 {
                font-size: 30px;
                line-height: 35px;
                padding-left: 120px;
                @include xs-block {
                    letter-spacing: -0.723529px;
                    word-break: break-word;
                }
                @include xl-block {
                    margin-bottom: 40px;
                }
            }

            p {
                font-size: 20px;
                line-height: 34px;
                font-weight: 300;
                color: #9297A2;
            }

            &:nth-child(1) {
                &::before {
                    content: "";

                    @include hyvw__item_icon;
                }
            }

            &:nth-child(2) {
                &::before {
                    content: " ";

                    @include hyvw__item_icon;
                }
            }

            &:nth-child(3) {
                &::before {
                    content: "";

                    @include hyvw__item_icon;
                }
            }

            &:nth-child(4) {
                &::before {
                    content: "";
                    color: red;

                    @include hyvw__item_icon;
                }
            }

            &:last-child {
                width: 100%;


                div {
                    padding-left: 120px;
                    @include xs-block {
                        margin-top: 100px;
                        padding: 0;
                    }
                    @include xl-block {
                        margin-top: 0;
                        padding-left: 120px;
                    }

                    p {
                        color: #000;
                        font-size: 18px;
                        line-height: 22px;
                        margin: 0;
                    }
                }

                &::before {
                    content: "";

                    @include hyvw__item_icon;

                    border: solid 20px rgba(252, 176, 71, 0.1);
                    height: 90px;
                    line-height: 50px;
                    border-radius: 38px;
                    @include xs-block {
                        top: -10px;
                    }
        
                }
            }
        }
    }
}

.how_earn .benefits__list .benefit__item {
    &:nth-child(1) {
        &::before {
            content: "";
        }
    }

    &:nth-child(2) {
        &::before {
            content: "";
        }
    }

    &:last-child {
        &::before {
            content: "";
            background: no-repeat url("../img/cash_symbol.png");
        }
    }
}

