h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

.container {
    @include container();
    @include xs(max-width, rem(315));
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}

.doc_container {
    @include container_doc();
    @include xs(max-width, rem(315));
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}



body {
    font-family: 'Roboto', sans-serif ;
}

a {
    text-decoration: none;
    color: #000;
}

ul, ol {
    list-style: none;
}

.white {
    color: #fff;
}

.bg_white {
    background: white;
}

.show {
    display: flex;
    opacity: 1;
    transition: opacity 0.2s ease-out;
    z-index: 99;
}

.hidden {
    display: flex;
    opacity: 0;
    transition: opacity 0.2s linear;
    z-index: -2;
}

.xs {
    @include xs(display, block);
  }
  
  .xs_n { 
    @include xs(display, none);
  }

  html {
      overflow-x: hidden;
  }
