.doc {
  @include section();
  flex-direction: column;
  width: 1140px;
  margin-bottom: 120px;

  .doc__title {
    @include xs-block {
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin-bottom: 30px;
    }
    @include xl-block {
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 60px;
      margin-top: 71px;
    }
    position: relative;
      display: flex;
 

      h1 {
       font-size: 48px;
       
       @include xl-block{
        text-align: left;
       }
       @include xs-block {
         order: 1;
       }
      }

      .policy {
        @include xs-block {
          order: 6;
          width: 150px;
        }
        @include xl-block {
          order: 6;
          width: 450px;
          text-align: right;
        }
        font-weight: bold;
        font-size: 28px;
        position: relative;
        top: 20px;
        position: relative;
     

        


        .selected_policy {
          padding-right: 40px;
          background: no-repeat url("../img/svg/Arr_for_select.svg");
          background-position: right 10px;
          cursor: pointer;
          @include xl-block {
            
          }

        }

        .conteiner_policy{
          @include xs-block {
            right: -90px;
          }
          width: 333px;
          position: absolute;
          align-items: center;
          justify-content: center;
          top: 56px;
          right: 0;
          box-shadow: 0px 10px 35px rgba(0, 0, 0, 0.202502);
          background: #fff;
          border-radius: 15px;
          

        .policy_item {
          text-align: center;
          
          padding: 39px 0;
          // width: 100%;
          cursor: pointer;
          

          &:hover {
              color: #0072FF;
              transition: color 0.3s;
          }

        }
        .policy_item_input {
          display: none;
        }
        }
      }

      .comment { 
        @include xs-block {
          order: 2;
          position: relative;
          top: 0;
          font-size: 16px;
          line-height: 34px;
          margin: 0;
        }
        @include xl-block {
          position: absolute;
          top: 70px;
          font-size: 22px;
          left: 2px;
        }
        font-size: 22px;
        line-height: 34px;
        color: #9297A2;
        font-weight: 300;
        
      }

  }

  .cookie_title {
    display: flex;
    flex-direction: column;

    @include xs-block {

      align-items: center;
    }
    @include xl-block {
      margin-top: 71px ;
      align-items: flex-start;
    }

    h1 {
      font-size: 48px;
      @include xs-block {
        text-align: center;
        margin-bottom: 20px;
        // order: 1;
      }
      @include xl-block {
        text-align: left;
        line-height: 54px;
      }
     }
     .comment { 
      @include xs-block {
        order: 2;
        font-size: 16px;
        line-height: 34px;
        margin: 0;
      }
      @include xl-block {
        font-size: 22px;
        line-height: 34px;
        text-align: left;
        margin-bottom: 20px;
      }
      font-size: 22px;
      line-height: 34px;
      color: #9297A2;
      font-weight: 300;
      
    }
     

  }
  


  .doc__text {
    @include xs-block {
    }
    font-size: 18px;
    line-height: 24px;
    font-weight: 300;
  }

}

.xs {
  @include xs(display, block);
}

.xs_n { 
  @include xs(display, none);
}

