@include xl-block { 
    .footer {
        display: flex;
        flex-direction: column;
    }
}
.footer{
    .footer_xl {
        @include xl(display, block);
        @include xs(display, none);
     
        
        .footer__navigation {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 40px;
        }
    
        .footer__navigation_link ul {
            display: flex;
            flex-direction: row;
            width: 542px;
            
            
            li {
                padding: 1.5%;
            }
            li:first-child {
                padding-left: 0;
            }
            
            li a{
                font-size: 18px;
                word-wrap: normal;
                font-weight: 300;
            }
        }
    
        .footer__socials {
            height: 36px;
            max-width: 210px;
     
            a {
                padding: 25px;
            }
        }
    
        .footer_copyright {
            font-size: 14px;
        }
    
    }

    .footer_xs {
        @include sm(display, none);
        @include size-xs(13);
        @include xs-block {
            margin-top: 60px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;

            .nav {
                height: 400px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                text-align: center;
                font-size: 28px;
                font-weight: 700;
                margin-bottom: 60px;
            }

            .socials {
                display: flex;
                justify-content: space-between;
                width: 185px;
                margin-bottom: 80px;
                img {
                    width: 60px;
                    height: auto;                    
                }
            }
            
            .footer_text {
                display: flex;
                justify-content: space-between;
                width: 100%;
                margin-bottom: 30px;
                li {
                    a{
                        font-size: 14px;
                    }
                }

            }
            .footer_copyright {
                font-size: 12px;
                margin-bottom: 30px;
            }
        }
    }
}




