.header {
    padding-top: 10vh;
    padding-bottom: 11.4vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include xs-block {
        padding-top: 62px;
        padding-bottom: 71px;
    }
}

.logo {
    @include col-ib();
    position: relative;
    @include size-xs(6);
    @include size-xl(4);
    img {

        max-width: 100%;
        height: auto;
    }
    sup {
        position: absolute;
        text-transform: uppercase;
        font-weight: 500;
       
        @include xs-block {
            top: 45px;
        }
        @include xl-block {
            top: 65px;
        }
    }
}

.lang {
    @include xs(display, none);
    @include xl(display, inherit);
    // @include size-xl(4);
    @include xl-block {
       left: -10%;
      
    }
    position: relative;

    .selected_lang {
        @include xs-block{
            width: 80px;
        }
        @include xl-block{
            width: 60px;
        }
        background: no-repeat url("../img/svg/Arr_for_select.svg");
        background-size: 25%;
        font-weight: 700;
        font-size: 24px;
        width: 60px;
        height: 40px;
        padding-right: 37px;
        background-position: 50px 8px;
        cursor: pointer;

        // transition: all 0.3s linear;

        // &:hover {
        //     color: #0072FF;
        //     background:no-repeat url("../img/svg/Arr_for_select _blue.svg");
        //     background-position: right 7px;
        //     transition: all 0.3s linear;
        // }
        
    }



    .conteiner_langs {
        width: 120px;
        height: 206px;
        position: absolute;
        background: no-repeat url("../img/svg/Check_lang_bg.svg");
        box-shadow: 0px 10px 35px rgba(0, 0, 0, 0.202502);
        // z-index: 2;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        left: -20px;
        top: 60px;
        transition: all 0.3s linear;

        

        .langs_item {
            font-weight: 700;
            font-size: 28px;
            cursor: pointer;

            &:hover {
                color: #0072FF;
                transition: color 0.3s;
            }          
        }

        input[type="radio"]:checked+label{ display: none; } 
        input[type="radio"] {
        display: none;
        }
    }

}



.burger_menue {
  @include col-ib();
  position: relative;

  top: 20px;
  left: 30px;
  z-index: 1;
  @include size-xs(2);
  @include xs-block {
        position: relative;
        top: 0;
        left: -10px;
}
@include xl-block {
    position: relative;
    top: 0;
    left: 210px;
}



 
    input {
        display: block;
        width: 36px;
        height: 35px;
        position: absolute;
        top: -7px;
        left: -5px;
        cursor: pointer;
        opacity: 0; 
        z-index: 2;
        @include xs-block {
           position: absolute;
        }

        &:checked ~ span {
            opacity: 1;
            transform: rotate(45deg) translate(-2px, -1px);
            background: #fff;
        }

        &:checked ~ span:nth-last-child(3) {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
        }

        &:checked ~ span:nth-last-child(2){
            transform: rotate(-45deg) translate(6px, -6px);
       
        }

        &:checked ~ .burger_menu__links {
            visibility: visible;
            transform: translateX(0);
            transition: transform 0.9s cubic-bezier(0.07,0.2,0.05,1.0);
        }


    }

    span {
        display: block;
        width: 35px;
        height: 4px;
        margin-bottom: 10px;
        position: relative; 
        background: #000;
        border-radius: 3px; 
        z-index: 1;
        transform-origin: 4px 0px;      
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s ease;
        
        &:first-child {
            transform-origin: 0% 0%;
        }

        &:nth-last-child(2) {
            transform-origin: 0% 100%;
          
        }
        
      }

      .burger_menu__links {
        box-sizing: border-box;
        color: #fff;
        position: fixed;
        top: 0;
        right: 0;
        height: 100vh;
        margin: 0;
        visibility: visible;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), #0C1723;
        transform: translateX(100%);
        transition: transform 0.5s linear;
        padding-bottom: 50px;

   
       

        @include xs-block {
            height: 100vh;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            width: 100%;
   
        }
        @include xl-block {
            width: 47vw;
            box-sizing: border-box;
            color: #fff;
            position: fixed;
            top: 0;
            right: 0;
            height: 100vh;
            margin: 0;
            visibility: visible;
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), #0C1723;
            transform: translateX(100%);
            transition: transform 0.5s linear;
            padding-bottom: 50px;
         
        }

        a{
            color:white;
        }



        .lang_for_xs {
            @include sm(display, none);
            @include xs-block {
                align-self: flex-start;
                // position: absolute;
                top: 58px;
                left: 30px;
                display: inline-block;
              
                .selected_lang {
                    color:white;
                    height: 30px;
                    font-weight: 700;
                    font-size: 22px;
                    background: no-repeat url("../img/svg/Arr_for_select _white.svg");
                    font-weight: 700;
                    font-size: 28px;
                    padding-right: 5px;
                    background-position: right 13px;
                } 
                .langs_item {
                    color: #000;
                }
            }
        }

            .burger_menu__navigation_link {
                position: relative;
                left: 20%;
                @include xl-block {
                    top: 83px;
                    left: -150px;
                    height: 400px;
                }
                @include xs-block {
                    flex-shrink: 1;
                    display: flex;
                    flex-direction: column;
                    // flex-wrap: wrap;
                    align-items: center;
                    height: 50%;
                    justify-content: space-between;
                   

                    // margin-top: 
                    top: 0;
                    left: 0;

                }


                li {
                    text-align: center;
                    padding: 10px 20px;
                    margin: 19px 0;
                    @include xs-block {
                        margin: 0;
                        &:nth-child(3) {
                            display: none;
                        }
                    }
                    @include xl-block {
                        margin: 0;
                        &:nth-child(3) {
                            display: inherit;
                        }
                    }
                    a {
                        font-size: 22px;
                        font-weight: 500;
                        @include xl-block {
                            font-weight: 400;
                            // margin-bottom: 40px;
                        }

                        &:hover {
                            color: #0072FF;
                            transition: color 0.3s;
                        }
                    }
                }
            }
      

      .burger_menu__socials {
        height: 36px ;
        display: flex;
        justify-content: space-between;
        position: absolute;
        bottom: 18.5vh;
        left: 20%;
        
        @include xs-block {
        position: relative;
        bottom: 0;
        left: 0;
  
        width: 185px;
            img {
                height: 60px;
            }
            height: 60px;
        }

        @include xl-block {
            position: relative;
            bottom: -150px;
            width: 185px;
            left: -150px;
            width: 132px;

            img {
                width: 36px;
                height: 36px;
            }
        }

        
    
      }

      .burger_menu__foote_text {
          position: absolutree;
          bottom: 69px;
          font-size: 16px;
          font-weight: 300;
          left:10%;

          @include xs-block {
              position: relative;
              bottom: 0;
              left: 0;
            //   top: 27vh;
          }
          @include xl-block {
            left: -150px;
        }
          a:hover {
              color: #0072FF;
              transition: color 0.3s;
          }
      }
    }
}
.header_xs_hidden {
    @include xs-block {
        display: none;
    }
    @include xl-block {
        display: flex;
    }
}